import React, { useRef } from 'react';
import zlib from 'zlib';
// import { FileConsumer } from '../../contexts/fileContext';
import { Button } from '@mui/material';
import PublishIcon from '@mui/icons-material/PublishRounded';
import { GameDecoder, Game, Chunk } from '../../custom_modules/GameFormat';

function FileImport({ setFile }: {
    setFile: React.Dispatch<React.SetStateAction<Game.Data>>
}) {
    const inputFile = useRef<HTMLInputElement>(null);
    const onButtonClick = () => {
        if (inputFile.current)
            inputFile.current.click();
    };
    const onFileChange = async () => {
        if (inputFile.current?.files)
            try {
                const game = new GameDecoder(zlib.inflateSync(
                    Buffer.from(
                        await inputFile.current?.files[0].arrayBuffer()
                    )
                )).decGame();
                // console.log(game);
                // let chunkNames: { [key: number]: string | null } = {};
                // game.chunks.forEach(value => {
                //     if (value.id)
                //         chunkNames[value.id] = value.name ?? chunkNames[value.id] ?? null
                // })
                // console.log(chunkNames);
                // game.chunks = game.chunks.filter((value) => {
                //     if (value.id)
                //         return chunkNames[value.id] !== null;
                //     return true;
                // })
                // console.log(game);
                setFile(game);
            } catch (e) {
                if ((e as Error).message === 'incorrect header check')
                    alert('Wrong file format!\nPlease choose a fancade game file.');
                else console.error(e);
            }
    }
    return (
        <Button variant="contained" size='large' startIcon={<PublishIcon />} onClick={onButtonClick} sx={{
            height: 'auto',
            borderRadius: '40px',
            paddingX: '15px',
            fontWeight: '600'
        }}>
            <input type='file' id='file' ref={inputFile} onChange={onFileChange} style={{ display: 'none' }} />
            Import
        </Button>
    )
}

export default FileImport